// Typography
// Headings
h1,
h2,
h3,
h4,
h5,
h6 {
	line-height: 1.2;
	margin-top: 0;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	&.header {
        background-color: $bg-color-gray;
        padding: $layout-spacing $layout-spacing-lg;
    }
}

$heading-padding-sm: 0.5rem;
$heading-padding-lg: 1rem;

h1,
.h1 {
    font-size: 4.625rem;
	margin-bottom: $heading-padding-lg;

	font-family: $heading-font-family;
    font-weight: bold;
}

h2,
.h2 {
	font-size: 2.75rem;
	margin-bottom: $heading-padding-lg;

	font-family: $heading-font-family;
    font-weight: bold;
}

h3,
.h3 {
	font-size: 2rem;
	margin-bottom: $heading-padding-lg;

	font-family: $heading-font-family;
    font-weight: bold;
}

h4,
.h4 {
	font-size: 1.375rem;
	margin-bottom: $heading-padding-sm;

	font-family: $heading-font-family;
    font-weight: bold;
}

h5,
.h5 {
	font-size: 1rem;
	margin-bottom: $heading-padding-sm;
}

h6,
.h6 {
	font-size: .8rem;
	margin-bottom: $heading-padding-sm;
}

// Paragraphs
p {
	margin: 0 0 $line-height;
}

// Semantic text elements
a,
ins,
u {
	text-decoration-skip: ink edges;
}

abbr[title] {
	border-bottom: $border-width dotted;
	cursor: help;
	text-decoration: none;
}

kbd {
	@include label-base();
	@include label-variant($light-color, $dark-color);
	font-size: $font-size-sm;
}

mark {
	@include label-variant($body-font-color, $highlight-color);
	border-bottom: $unit-o solid darken($highlight-color, 15%);
	border-radius: $border-radius;
	padding: $unit-o $unit-h 0;
}

// Blockquote
blockquote {
	border-left: $border-width-lg solid $border-color;
	margin-left: 0;
	padding: $unit-2 $unit-4;

	p:last-child {
		margin-bottom: 0;
	}
}

// Lists
ul,
ol {
	margin: $unit-4 0 $unit-4 $unit-4;
	padding: 0;

	ul,
	ol {
		margin: $unit-4 0 $unit-4 $unit-4;
	}

	li {
		margin-top: $unit-2;
	}
}

ul {
	list-style: disc inside;

	ul {
		list-style-type: circle;
	}
}

ol {
	list-style: decimal inside;

	ol {
		list-style-type: lower-alpha;
	}
}

dl {
	dt {
		font-weight: bold;
	}

	dd {
		margin: $unit-2 0 $unit-4 0;
	}
}